.reservation {
  background-color: var(--light-color);

  &__container {
@include flex-column-center;
}
  &__content {
    padding-bottom: 60px;
    width: 70%;

    @include big-desktop {
      padding-bottom: 40px;
      width: 80%;
    }


  }
}
.reservation-form {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  border-bottom: 1px solid var(--gray-light-color);
  padding-bottom: 60px;

  &__btn {
    border-radius: 0 5px 5px 0 ;
    padding: 0 100px;
    font: var(--regular-ubuntu);

    @include tablet {
      border-radius: 5px;
      padding: 10px 100px;
    }
  }
}
.reservation-form-field {
  margin: 0;
  padding: 0;
  grid-column: 1 span;

  @include tablet {
    grid-column: 12 span;
    &:not(:last-child) {
      padding-bottom: 20px;
    }

  }

&__select {
    margin-right: 0;
    border-style: solid;
    border-width: 1px;
    border-color: var(--gray-light-color);
    padding: 20px 45px;
    width: 100%;
    font: var(--regular-ubuntu);
    text-align: start;
    color: var(--gray-light-color);
    background-position: 10px 50%;
    background-repeat: no-repeat;

    &--data {
      border-radius: 5px 0 0 5px;
      border-right: 0;
      background-image: url("../img/data.png");


}
    &--people {
      background-image: url("../img/people.png");
      border-right: 0;
}

    &--hour {
      border-right: 0;
      background-image: url("../img/hour.png");
    }

    @include tablet {
      border: 1px solid var(--gray-light-color);
      //border-right: 1px solid var(--gray-light-color);
      border-radius: 5px;
      //background-image: url("../img/hour.png");
    }

    }


}
.reservation-explain {
  margin-top: 60px;
  font: var(--regular-ubuntu);
  font-size: 24px;
  line-height: 160%;
  text-align: center;
  color: var(--text-light-color);
}
.reservation-flower {
  @include flex-column-center;
}

